let onAuthCallback = null;

// Function to handle Telegram authentication callback
export function onTelegramAuth(user) {
    console.log(`Logged in as ${user.first_name} ${user.last_name} (${user.id}${user.username ? ', @' + user.username : ''})`);
    if (onAuthCallback) {
        onAuthCallback(user);
    }
}

// Function to dynamically load Telegram widget script
export function loadTelegramWidget(callback) {
    if (!window.Telegram) {
        console.error('Telegram Web Apps SDK is not loaded.');
        return;
    }

    // Check if Telegram user data is already available
    if (window.Telegram.WebApp && window.Telegram.WebApp.initDataUnsafe && window.Telegram.WebApp.initDataUnsafe.user) {
        console.log('User is already authenticated:', window.Telegram.WebApp.initDataUnsafe.user);
        if (callback) {
            callback(window.Telegram.WebApp.initDataUnsafe.user);
        }
        return;
    }

    onAuthCallback = callback;

    // Load Telegram widget script dynamically
    const script = document.createElement('script');
    script.src = 'https://telegram.org/js/telegram-widget.js?22';
    script.async = true;
    script.setAttribute('data-telegram-login', 'samplebot'); // Replace with your bot username or authorization key
    script.setAttribute('data-size', 'small');
    script.setAttribute('data-userpic', 'false');
    script.setAttribute('data-onauth', 'onTelegramAuth(user)');
    script.setAttribute('data-request-access', 'write');
    script.setAttribute('data-lang', 'en'); // Specify the language code here

    // Append script to the document body
    document.body.appendChild(script);

    // Clean up function to remove the script when the component unmounts or when not needed
    return () => {
        document.body.removeChild(script);
        delete window.onTelegramAuth; // Clean up the global function
    };
}
